@import '../../style/variables.scss';

.randomchar {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);

    &__block {
        padding: 40px 35px;
        display: grid;
        grid-template-columns: 180px auto;
        column-gap: 30px;

        &-node {
            &-enter {
                opacity: 0;
                transform: scale(1.1);

                &-active {
                    opacity: 1;
                    transform: scale(1);
                    transition: opacity 450ms, transform 450ms;
                }
            }

            &-exit {
                opacity: 1;
                transform: scale(1);

                &-active {
                    opacity: 0;
                    transform: scale(0.9);
                    transition: opacity 450ms, transform 450ms;
                }
            }
        }
    }

    &__img {
        width: 180px;
        height: 180px;
        object-fit: cover;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-height: 100%;
        gap: 10px;
        padding-top: 3px;
    }

    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
        margin-bottom: 8px;
    }

    &__descr {
        font-size: 14px;
        line-height: 18px;
        max-height: 80px;
        padding-bottom: 8px;
        flex: 1 1 auto;
        align-self: stretch;

        overflow-y: auto;
        scroll-behavior: smooth;

        scroll-snap-align: none;
        scrollbar-color: #5C5C5C lighten($color: #5C5C5C, $amount: 40%);
        scrollbar-width: thin;
        scrollbar-gutter: stable;

        &::-webkit-scrollbar {
            width: 1.2px;
        }

        &::-webkit-scrollbar-track {
            // background: lighten($color: #5C5C5C, $amount: 40%);
            background: transparent;
            border-radius: 100px;
        }

        &::-webkit-scrollbar-thumb {
            background: #5C5C5C;
            border-radius: 100px;
        }
    }

    &__btns {
        a:nth-child(1) {
            margin-right: 30px;
        }
    }

    &__static {
        padding: 40px 35px;
        background-color: $dark;
        position: relative;

        button {
            margin-top: 13px;
        }
    }

    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.045em;
        color: #FFFFFF;

        &:nth-child(2) {
            margin-top: 33px;
        }
    }

    &__decoration {
        position: absolute;
        bottom: 14px;
        right: -37px;
    }

    &-node {
        &-enter {
            opacity: 0;
            transform: scale(1.1);

            &-active {
                opacity: 1;
                transform: scale(1);
                transition: opacity 450ms, transform 450ms;
            }
        }

        &-exit {
            opacity: 1;
            transform: scale(1);

            &-active {
                opacity: 0;
                transform: scale(0.9);
                transition: opacity 450ms, transform 450ms;
            }
        }
    }
}